<template>
  <el-card shadow="never" :body-style="{padding: '0 10px'}">
    <el-form :inline="true" :model="form" :rules="rules" ref="form">
      <el-form-item prop="title" style="width: 60em">
        <el-input v-model="form.title" placeholder=""
                  :style="{width: '60rem', marginTop: '1px'}"
                  maxlength="30"
                  show-word-limit></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="info" @click="save('form')"></el-button>
        <el-button type="primary" @click="release('form')"></el-button>
      </el-form-item>
    </el-form>
    <el-row :gutter="10">
      <el-col :span="12">
        <quill-editor class="editor"
                      v-model="content"
                      ref="quillEditor"
                      :style="{height:fuillH+'px'}"
                      :options="editorOption"
                      @blur="onEditorBlur($event)"
                      @focus="onEditorFocus($event)"
                      @ready="onEditorReady($event)">
        </quill-editor>
      </el-col>
      <el-col :span="12">
        <div class="output ql-snow" :style="{height: outputH+'px',border: '1px solid #C0C4CC'}">
          <div class="ql-editor" v-html="this.content"></div>
        </div>
      </el-col>
    </el-row>
    <ArticleDialog v-if="visible == true"
                   :visible="visible"
                   :data="dataArray"
                   @close-before="closeBefore"
                   @submit-Article="submitArticle">
    </ArticleDialog>
  </el-card>
</template>

<script>
  //vue-fuill-editor
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'
  import {quillEditor} from 'vue-quill-editor'

  //highlight.js
  import 'highlight.js/styles/atelier-dune-dark.css'
  import hljs from 'highlight.js/lib/highlight';
  import javascript from 'highlight.js/lib/languages/javascript';
  import java from 'highlight.js/lib/languages/java'

  hljs.registerLanguage('javascript', javascript);
  hljs.registerLanguage('java', java)

  import ArticleDialog from "./ArticleDialog";
  import {axiosUtil} from "../network/axiosUtil";

  export default {
    name: "QuillEditor",
    data() {
      return {
        content: '', //
        visible: false, //
        dataArray: [], //ArticleDialog
        editorOption: {
          modules: {
            toolbar: [
              ['bold', 'italic', 'underline', 'strike'],    //
              ['blockquote', 'code-block'],     //
              [{'header': [1, 2, 3, 4, 5, 6, false]}],     //
              [{'size': ['small', false, 'large', 'huge']}], // 
              [{'list': 'ordered'}, {'list': 'bullet'}],     //
              [{'indent': '-1'}, {'indent': '+1'}],     // 
              [{'direction': 'rtl'}],             // 
              [{'color': []}, {'background': []}],     // 
              [{'font': []}],     //
              [{'align': []}],    //
              ['clean'], //
              ['link', 'image', 'video'] //
            ],
            syntax: {
              highlight: text => hljs.highlightAuto(text).value
            }
          },
          theme: 'snow'
        },
        form: {
          title: ''
        },
        rules: {
          title: [
            {required: true, message: '', trigger: 'blur'},
            {min: 5, max: 30, message: '5', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      onEditorBlur(quill) {
        // console.log('editor blur!', quill)
      },
      onEditorFocus(quill) {
        // console.log('editor focus!', quill)
      },
      onEditorReady(quill) {
        // console.log('editor ready!', quill)
      },
      save(form) { //
        this.$refs[form].validate(valid => {
          if(valid) {
            alert("...")
          }
        })
      },
      release(form) { //
        this.$refs[form].validate(valid => {
          if(valid) {
            this.visible = true
          }
        })
      },
      closeBefore(state) {
        this.visible = state
      },
      submitArticle(state, category, type, editState) {
        this.visible = state
        if(editState) { //
          axiosUtil({
            url: '/updateArticle',
            method: 'post',
            data: {
              title: this.form.title,
              content: this.content,
              category: category,
              type: type,
              articleNum: this.$route.query.articleNum
            }
          }).then(res => {
            if(res.code === 19) {
              this.$message({
                type: 'success',
                message: ''
              })
              //
              this.form.title = ''
              this.content = ''
            } else if(res.code === 20) {
              this.$message.error("")
            } else if(res.code === "010") {
              this.$message.error("")
            }
          })
        } else { //
          //...
          axiosUtil({
            url: '/submitArticle',
            method: 'post',
            data: {
              title: this.form.title,
              content: this.content,
              category: category,
              type: type
            }
          }).then(res => {
            if(res.code === 10) {
              this.$message({
                type: 'error',
                message: res.msg
              })
            } else if(res.code === 12) {
              this.$message({
                type: 'error',
                message: res.msg
              })
            } else if(res.code === 11) {
              this.$message({
                type: 'success',
                message: res.msg
              })
              //
              this.form.title = ''
              this.content = ''
            }
          }).catch(err => {
            console.log(err);
          })
        }
      }
    },
    computed: {
      fuillH() { //fuillEditor
        return document.documentElement.clientHeight - 258
      },
      outputH() { //
        return document.documentElement.clientHeight - 192
      }
    },
    components: {
      quillEditor,
      ArticleDialog
    },
    mounted() {
      //
      axiosUtil({
        url: '/getArticle',
        method: 'get',
        params: {
          articleNum: this.$route.query.articleNum
        }
      }).then(res => {
        if(res.code === 200) {
          this.content = res.data.content
          this.form.title = res.data.title
          this.dataArray.push({
            type: res.data.type,
            category: res.data.category,
            edit: true //
          })
        }
      })
    }
  }
</script>

