<template>
  <el-dialog title="" :visible="visible" width="40%" :before-close="closeBefore">
    <el-form :model="form" :rules="rules" ref="form" label-width="100px">
      <el-form-item label="" prop="category">
        <el-select v-model="form.category" filterable allow-create default-first-option placeholder="">
          <el-option v-for="item in form.categorys"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="form.type" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <span :style="{color: '#F56C6C'}">**</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="submitArticle('form')"></el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    name: "ArticleDialog",
    props: {
      visible: {
        type: Boolean,
        required: true
      },
      data: {
        type: Array
      }
    },
    data() {
      return {
        form: {
          type: '',
          category: '',
          categorys: [{  //
            value: 'HTML',
            label: 'HTML'
          }, {
            value: 'CSS',
            label: 'CSS'
          }, {
            value: 'JavaScript',
            label: 'JavaScript'
          }]
        },
        rules: {
          category: [
            {required: true, message: '', trigger: 'change'}
          ],
          type: [
            {required: true, message: '', trigger: 'change'}
          ]
        }
      }
    },
    methods: {
      closeBefore(done) {
        this.$emit("close-before", false)
        done()
      },
      submitArticle(form) {
        this.$refs[form].validate(valid => {
          if (valid) {
            if(this.data[0].edit) {
              this.$emit("submit-Article", false, this.form.category, this.form.type, true)
            } else {
              this.$emit("submit-Article", false, this.form.category, this.form.type)
            }
          } else {
            return false
          }
        })
      }
    },
    created() {
      if (this.data[0].length != 0) {
        let res = this.data[0]
        this.form.type = res.type
        this.form.category = res.category
      }
    }
  }
</script>

